#top,
#right,
#bottom,
#left {
  background: #fffbdc;
  position: fixed;
  z-index: 9999;
}
#left,
#right {
  top: 0;
  bottom: 0;
  width: 20px;
}
#left {
  left: 0;
}
#right {
  right: 0;
}

#top,
#bottom {
  left: 0;
  right: 0;
  height: 20px;
}
#top {
  top: 0;
}
#bottom {
  bottom: 0;
}

.Title {
  background: transparent;
  position: absolute;
  width: 400px;
  height: 700px;
  top: calc(50% - 350px);
  left: calc(50% - 200px);
  z-index: 15;
}

.Palmite {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: transparent;
  z-index: 10;
}

.palmita1 {
  position: absolute;
  right: -150px;
}
.palmita2 {
  position: absolute;
  left: -150px;
}

@media (max-width: 320px) {
  .Title {
    width: 300px;
    height: 0px;
    top: calc(20% - 100px);
    left: calc(50% - 150px);
  }
  .palmita1 {
    position: absolute;
    right: -80px;
  }
  .palmita2 {
    position: absolute;
    left: -80px;
  }
}

.WaterStyle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-size: contain;
  background-position: center;
  background-repeat: repeat;
  z-index: 1;
}

.PoolEl {
  position: absolute;
  width: 200px;
  height: 200px;

  z-index: 4;
  background-size: contain;
}

.el1 {
  left: 0;
  bottom: calc(10% - 100px);
}
.el2 {
  right: -40px;
  top: calc(65% - 100px);
}
.el3 {
  right: 0;
  top: 0;
}
.el4 {
  left: 0;
  bottom: calc(10% - 100px);
}
.el5 {
  left: 0;
  bottom: calc(40% - 100px);
}
.el6 {
  left: 0;
  top: calc(10% - 100px);
}

@media (min-width: 1281px) {
  .Title {
    width: 500px;
    height: 0px;
    top: calc(10% - 100px);
    left: calc(50% - 250px);
  }
  .el1 {
    left: calc(30% - 100px);
    bottom: calc(10% - 100px);
  }
  .el2 {
    right: calc(30% - 100px);
    top: calc(65% - 100px);
  }
  .el3 {
    right: 0;
    top: 0;
  }
  .el4 {
    left: calc(30% - 100px);
    bottom: calc(10% - 100px);
  }
  .el5 {
    left: calc(37% - 100px);
    bottom: calc(50% - 100px);
  }
  .el6 {
    left: 0;
    top: calc(10% - 100px);
  }
}
