html,
body {
  background: #20a2af;
  background-attachment: fixed;
  height: 100%;
  margin: 0;
  overflow: hidden;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

h1 {
  position: absolute;
  left: calc(50% - 150px);
  top: calc(25% - 100px);
  width: 300px;
  height: 200px;
  letter-spacing: -1.5px;
  line-height: 34px;
  color: #ffd636;
  font-size: 2.5em;
}
h2 {
  letter-spacing: -1.5px;
  line-height: 21px;
  color: #ffd636;
}

.Title {
  position: absolute;
  left: calc(50% - 150px);
  top: calc(73% - 10px);
  width: 300px;
}
.Info {
  background: none;
  outline: none;
  border: none;
  color: #ec1862;
  font-size: 1.3em;
  font-weight: 900;
  background: #fffbdc;
  border-radius: 30px 30px 0 30px;
  margin-top: 30px;
  padding: 16px 8px;
}
.InfoPosition {
  position: absolute;
  right: 10px;
  bottom: 10px;
  top: auto;
  left: auto;
  z-index: 999;
}
.App {
  position: absolute;
  top: 0;
  min-height: 100vh;
  min-width: 100vw;
  font-family: sans-serif;
  text-align: center;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.Copyright {
  position: absolute;
  left: calc(50% - 100px);
  width: 200px;
  bottom: 30px;
  background: none;
  outline: none;
  border: none;
  color: white;
  font-size: 10px;
  z-index: 99;
}

.Paperella {
  position: absolute;
  left: calc(50% - 120px);
  top: calc(85% - 75px);
  width: 200px;
  padding: 20px;
  background: none;
  outline: none;
  border: none;
  color: black;
  font-size: 0.9em;
  font-weight: 700;
  background: #fde472;
  border-radius: 30px;
  margin-top: 5px;
  z-index: 99;
}
.Paperella > a {
  color: black;
}

.PaperellaLink {
  width: 100%;
  height: 100%;
  display: flex;
  justify-items: center;
  align-content: center;
  background: none;
  outline: none;
  border: none;
  color: white;
}
.PaperellaImg {
  width: 100%;
  height: 100%;
}

.ReactModal__Overlay {
  z-index: 999;
}
